*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 50%;
}

body {
  box-sizing: border-box;
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  background-color: #EBE7DE;
  line-height: 1.7;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.7;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #000;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.container {
  width: 120rem;
  max-width: 95%;
  margin: 0 auto;
}

h1, h2, .heading-white, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.2;
}

h1 {
  font-size: 4rem;
}

h2, .heading-white {
  font-size: 3.6rem;
  color: #5E311E;
}
h2 span, .heading-white span {
  color: #C39C63;
  font-size: 1.8rem;
  display: block;
  margin-bottom: 1.2rem;
}
h2::after, .heading-white::after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 10rem;
  height: 10rem;
  background-image: url("../img/cafe.svg");
  background-size: 10rem;
}

.heading-white {
  color: #FFF;
}
.heading-white span {
  color: #FFF;
}
.heading-white::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 48em) {
  .header {
    height: 85vh;
  }
}
.header::before {
  position: absolute;
  display: inline-block;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.header--first {
  background-image: url("../img/header_inicio.jpg");
}
.header--us {
  background-image: url("../img/header_nosotros.jpg");
}
.header--process {
  background-image: url("../img/header_proceso.jpg");
}
.header--menu {
  background-image: url("../img/header_menu.jpg");
  background-position: right;
}
.header--gallery {
  background-image: url("../img/header_galeria.jpg");
  background-position: right;
}
.header--contact {
  background-image: url("../img/header_contacto.jpg");
}
.header__container {
  position: relative;
}
.header__logo {
  width: 50%;
  margin: 0 auto 5rem;
}
@media (min-width: 48em) {
  .header__logo {
    width: 20rem;
    margin: 0;
  }
}
.header__text h1 {
  color: #FFF;
}

.menu {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  margin-bottom: 5rem;
}
@media (min-width: 48em) {
  .menu {
    flex-direction: row;
    margin: 0;
  }
}
.menu__link:link, .menu__link:visited {
  color: #FFF;
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: all 0.4s;
}
.menu__link:hover, .menu__link:active {
  color: #C39C63;
}
.menu .selected:link,
.menu .selected:visited {
  color: #C39C63;
}

.bar {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
@media (min-width: 48em) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10rem;
  }
}

.footer {
  background-color: #5E311E;
}
.footer__container {
  padding: 5rem;
}
@media (min-width: 48em) {
  .footer__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.footer__box {
  font-family: "Bona Nova", sans-serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 48em) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__box h3 {
  font-family: inherit;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #C39C63;
}
.footer__box p, .footer__box a {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.footer__copy {
  text-align: center;
  color: #FFF;
  padding: 2rem;
}

.main__container {
  background-color: #FFF;
  padding: 5rem 3rem;
  max-width: 90%;
}
@media (min-width: 48em) {
  .main__container {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.section__icons {
  margin-top: 5rem;
}
@media (min-width: 48em) {
  .section__icons {
    margin-top: 10rem;
  }
}
@media (min-width: 48em) {
  .section__icons .list-icons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.section__icons .list-icons__item {
  margin-bottom: 5rem;
}
.section__icons .list-icons__item img {
  margin: 0 auto;
  width: 12rem;
}
.section__icons .list-icons__item p {
  font-family: "Bona Nova", sans-serif;
  font-size: 3.2rem;
  color: #5E311E;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 64em) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 64em) {
  .gallery__img:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery__img:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .gallery__img:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery__img:nth-child(2) img {
    width: 62rem;
  }
  .gallery__img:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery__img:nth-child(4) img {
    height: 62rem;
  }
  .gallery__img:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery__img:nth-child(5) img {
    width: 62rem;
  }
  .gallery__img:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery__img:nth-child(6) img {
    width: 62rem;
  }
  .gallery__img:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery__img:nth-child(7) img {
    height: 62rem;
  }
  .gallery__img:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery__img:nth-child(8) img {
    width: 94rem;
  }
  .gallery__img:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery__img:nth-child(9) img {
    width: 62rem;
  }
  .gallery__img:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery__img:nth-child(10) img {
    width: 62rem;
  }
}
.gallery__img img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}

.section__food {
  padding: 15rem 2rem 20rem 2rem;
  background-image: url("../img/menu_arriba.svg"), url("../img/menu_abajo.svg"), linear-gradient(to bottom, #5E311E, #5E311E);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
}
@media (min-width: 48em) {
  .section__food {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 64em) {
  .section__food {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 48em) {
  .grid-food {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.food h3 {
  color: #FFF;
}
.food li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.food li p {
  color: #FFF;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 1rem 0;
}
.food li p span {
  display: block;
  color: #C39C63;
}
.food li .precio {
  font-size: 4rem;
  font-weight: 700;
}

.section__testimonials {
  padding: 10rem 2rem;
}
@media (min-width: 48em) {
  .section__testimonials {
    padding: 10rem;
  }
}
.section__testimonials .testimonial {
  background-color: #EBE7DE;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
}
.section__testimonials .testimonial .author {
  display: block;
  padding: 2rem 0;
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Bona Nova", sans-serif;
}

@media (min-width: 48em) {
  .us {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.us__image {
  margin-bottom: 8rem;
}
@media (min-width: 48em) {
  .us__image {
    margin-right: 2rem;
    margin-bottom: 0;
  }
}
.us__image img {
  transform: rotate(-6deg);
  border: 3rem solid #FFF;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.us__text blockquote {
  padding: 1rem;
  margin: 3rem 0;
  font-size: 2rem;
  border-left: 0.5rem solid #5E311E;
}

.process {
  margin-bottom: 5rem;
}
.process:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 48em) {
  .process {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.process__image {
  flex: 1;
}
.process__text {
  flex: 1;
}
.process__text h3 {
  margin: 2rem 0;
  text-align: left;
  color: #5E311E;
}
.process__text h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #C39C63;
}
.process:nth-child(even) .process__image {
  order: 1;
}

.section__menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/4;
}
@media (min-width: 48em) {
  .section__menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.section__menu__text p {
  font-size: 2rem;
}

@media (min-width: 48em) {
  .contact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.form__field {
  margin-bottom: 2rem;
}
.form__field:last-child {
  margin-bottom: 0;
}
.form__field label {
  text-transform: uppercase;
  display: block;
  font-size: 2rem;
  font-weight: 700;
}
.form__field input:not([type=submit]),
.form__field select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5E311E;
  font-family: inherit;
}
.form__field input[type=submit] {
  font-family: inherit;
  background-color: #5E311E;
  color: #FFF;
  width: 100%;
  border: none;
  padding: 1rem;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
}
.form__field input[type=submit]:hover {
  cursor: pointer;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5E311E;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #FFF;
}

.padding-bottom-0 {
  padding-bottom: 0;
}